import axios from "axios";

export default {
  getAllEmployees: function() {
    return axios.get("https://glacial-falls-40496.herokuapp.com/api/allemployees");
  },
  getDepartmentofEmployee: function(department) {
    return axios.get("https://glacial-falls-40496.herokuapp.com/api/departments/" + department );
  },
  getDepartmentsList: function() {
    return axios.get("https://glacial-falls-40496.herokuapp.com/api/departmentslist");
  }
};
